::-webkit-scrollbar {
  width: 0px;
}
.fill-window {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  height: 100vh;
}
.close {
  --color: greenyellow;
  --hover: greenyellow;
}
.close:hover,
.close:focus {
  box-shadow: inset -4em 0 0 0 var(--hover), inset 4em 0 0 0 var(--hover);
  color: black !important;
}
.buttons {
  color: var(--color);
  transition: 0.25s;
}
.buttons:hover,
.buttons:focus {
  border-color: var(--hover);
  color: #fff;
}
.buttons {
  font-family: "Courier Prime", monospace;
  background: none;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  margin: 0.5em;
  padding: 1em 1em;
}
#card {
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(17, 25, 40, 0.75);
  border-radius: 12px;
  border: 2px solid rgba(12, 243, 43, 0.952);
}
#registeredForm {
  font-family: "cairo", sans-serif;
}
.form-banner {
  font-family: "Courier Prime", monospace;
  font-size: 2rem;
}
.form-control,
.form-control:focus,
.form-control:active {
  background: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid gray;
  box-shadow: inset 0 -1px 0 gray;
  color: #fff;
}
@media (max-width: 480px) {
  .form-banner {
    font-size: 1.5rem;
  }
}
@media (max-width: 400px) {
  .form-banner {
    font-size: 1rem;
  }
}

body {
  background: url(/static/media/matrix.346ed985.gif);
}

.fill-window {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  height: 100vh;
}

.title {
  font-family: "Courier Prime", monospace;
  font-size: 3rem;
  text-align: center;
  line-height: 3rem;
}

#csi-logo {
  position: fixed;
  top: 0;
  left: 0;
  width: 14rem;
}
.headers {
  border-bottom: 1px solid #fff;
}

.description {
  font-family: "Cairo", sans-serif;
  line-height: 47px;
  font-size: 1.8rem;
}
.pulse:hover,
.pulse:focus {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  box-shadow: 0 0 0 2em rgba(255, 255, 255, 0);
}
.pulse {
  --color: greenyellow;
  --hover: lightgreen;
}
@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--hover);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--hover);
  }
}
.buttons {
  color: var(--color);
  transition: 0.25s;
}
.buttons:hover,
.buttons:focus {
  border-color: var(--hover);
  color: #fff;
}
.buttons {
  font-family: "Courier Prime", monospace;
  background: none;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  margin: 0.5em;
  padding: 1em 2em;
}

.button {
  font-family: "Courier Prime", monospace;
  font-size: 1.2rem;
}

svg text {
  font-family: "Cairo", sans-serif;
  letter-spacing: 5px;
  stroke: #adff2f;
  font-size: 5rem;
  font-weight: 700;
  margin-top: 20rem;
  stroke-width: 2;
  -webkit-animation: textAnimate 5s infinite alternate;
          animation: textAnimate 5s infinite alternate;
}

@-webkit-keyframes textAnimate {
  0% {
    stroke-dasharray: 0 50%;
    stroke-dashoffset: 20%;
    fill: #fff;
  }
  100% {
    stroke-dasharray: 50% 0;
    stroke-dashoffstet: -20%;
    fill: hsla(189, 68%, 75%, 0%);
  }
}

@keyframes textAnimate {
  0% {
    stroke-dasharray: 0 50%;
    stroke-dashoffset: 20%;
    fill: #fff;
  }
  100% {
    stroke-dasharray: 50% 0;
    stroke-dashoffstet: -20%;
    fill: hsla(189, 68%, 75%, 0%);
  }
}

@media screen and (max-width: 520px) {
  .glow-text {
    font-size: 3rem;
  }
  .description {
    font-size: 1.5rem;
  }
  .title {
    font-size: 2rem;
  }
  #csi-logo {
    max-width: 10rem;
    max-height: 10rem;
  }
}

@media screen and (max-width: 300px) {
  .glow-text {
    font-size: 2.5rem;
  }
  .description {
    font-size: 1.3rem;
  }
  .title {
    font-size: 2rem;
  }
  #csi-logo {
    max-width: 8rem;
    max-height: 8rem;
  }
}

